import React from 'react'
import { Container } from 'react-bootstrap'
import Accordion from './Accordion/Accordion'
import './Guestions.css'
import { Link } from 'react-scroll'

const Guestions = () => {
    return (
        <>
            <Container fluid className='guestionsContainer' id="guestionsContainer">
                <Container className='d-flex justify-content-center flex-column align-items-center'>
                    <h2 className='guestionsCaption'>Вопросы и ответы</h2>
                    <Accordion caption={'Что из себя представляет карта «Лахза»? В чем ее преимущество перед другими картами?'} text={`Карта «Лахза» – это универсальная кредитно-дебетовая банковская карта от «Хумо» и «МегаФон Таджикистан», с помощью которой можно:
получать кредиты на сумму до 3000 сомони и сроком до 30 дней
приумножать собственные сбережения со ставкой 6% годовых
получать денежные переводы из России
Карта «Лахза» является уникальной на финансовом рынке Таджикистана, представляя из себя только одну пластиковую карту с поддержкой нескольких банковских услуг.`}/>
                    <Accordion caption={`Каким способом можно приобрести карту «Лахза»?`} text={`Карту «Лахза» может оформить каждый клиент «МегаФон Таджикистан», имеющий активный номер, зарегистрированный на паспортные данные клиента. Номер клиента будет считаться его идентификатором для оформления и дальнейшего пользования картой «Лахза». Оформить карту можно в любом салоне связи «МегаФон Таджикистан» (кроме ГБАО) или на данной сайте.`}/>
                    <Accordion caption={`Я заказал карту «Лахза» на этом сайте? Когда я смогу ее получить? Услуга доставки платная?`} text={`Процесс доставки карты «Лахза» осуществляется в течение одного дня в пределах г. Душанбе. Стоимость доставки составляет 10 сомони.`}/>
                    <Accordion caption={`Сколько стоит приобретение и обслуживание карты «Лахза»?`} text={`Карты «Лахза» оформляются и обслуживаются бесплатно.`}/>
                    <Accordion caption={`Какой лимит на получение кредита доступен на карте «Лахза»?`} text={`При оформлении карты «Лахза» предусмотрены лимиты овердрафта от 500 до 3000 сомони. Лимит в размере 500 сомони является гарантированным для всех клиентов.`}/>
                    <Accordion caption={`Установленный при оформлении карты кредитный лимит является окончательным или может изменяться?`} text={`Установленный при оформлении карты «Лахза» лимит овердрафта может увеличиваться и уменьшаться в зависимости от действий клиента, производимых им данной картой.`}/>
                    <Accordion caption={`Какая комиссия предусмотрена за использование овердрафта для держателей карты «Лахза»?`} text={`Условия предоставления овердрафта для держателей карты «Лахза»:
Оплата покупок через POS-терминалы «Хумо» – 0%
Выдача наличных денег через банкоматы (АТМ) и в пунктах выдачи наличных – 4%
Остальные финансовые операции – 3%
За совершение финансовых операций с дебетового счета карты комиссия не предусмотрена.`}/>
                    <Accordion caption={`Я смогу получать проценты, если буду хранить деньги на карте «Лахза»?`} text={`При условии, если на дебетовом счете карты «Лахза» будут храниться Ваши сбережения, по окончании каждого дня будет производить расчет депозита со ставкой 6% годовых. По итогам месяца на Вашу карту будет произведено начисление суммы всех ежедневных процентов.`}/>
                    <Accordion caption={`Каким способом я могу получить денежные переводы с России на карту «Лахза»?

`} text={`Отправитель денежного перевода с России на карту «Лахза» может воспользоваться следующими сервисами и услугами:

Приложение «Хумо Переводы» или другие приложения банков РТ и РФ с возможностью совершать денежные переводы
QIWI-кошелек
Отделения и ЦБО банков РФ
Терминалы по оплате услуг с поддержкой отправки перевода денег в РТ
Через сайт денежных переводов МегаФон Таджикистан (perevod.megafon.tj)`}/>
                    <Accordion caption={`Какие платежные системы поддерживаются картой «Лахза»?`} text={`В настоящий момент выпускаются карты «Лахза» национальной платежной системы «Корти милли».`}/>
                    <Accordion caption={`Каков срок действия карты «Лахза»?`} text={`Срок действия карты «Лахза» составляет 18 месяцев с момента ее выпуска.`}/>
                    <Link
                        activeClass="active"
                        to="download"
                        spy={true}
                        smooth={true}
                        duration={500}>
                        <button className='questionsBtn' id="questionsBtn">Заказать карту</button>
                    </Link>
                </Container>
            </Container>
        </>
    )
}
export default Guestions;