import React from 'react'
import './Product.css'
import { Container } from 'react-bootstrap'
import { Link} from "react-scroll"
const Product = () => {
    return (
        <Container className='productContainer'> 
            <div className='productImg'>
                <div className='productCardText'>
                    <h1 className='productCaption'>
                        Лахза - больше<br/> чем банковская карта!
                    </h1>
                    <p className='productText'>
                       Кредиты до 3000 сомони сроком на 30 дней – это раз<br/>
                       Накопления сбережений со ставкой 6% годовых – это два<br/>
                       Денежные переводы из России – это три
                    </p>
                    <div className='btnBox'>
                        <Link
                            activeClass="active"
                            to="autorization"
                            spy={true}
                            smooth={true}
                            duration={500}
                        >   
                            <button className='productBtn' id='productBtn'>Заказать карту</button>
                        </Link>
                    </div>
                </div> 
            </div>
        </Container>
    )
}
export default Product;