import React from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import './Footer.css'
import telegramm from '../../assets/telegramm.svg';
import facebook from '../../assets/facebook.svg';
import vk from '../../assets/vk.svg';
import instagramm from '../../assets/instagramm.svg';
const Footer = () => {
    return (
        <>
        <Container className='footerContainer'>
            <Row>
                <Col className='col-12 col-md-7 col-lg-7'>
                    <h3 className='footerCaption'>О нас</h3>
                    <p className='footerText'>Прогрессивная и одна из лидирующих микрофинансовых организаций в Таджикистане, предоставляющая банковские услуги более 100 тысячам клиентов.</p>
                    <p className='footerAdress'>734061, г. Душанбе, ул. Н. Карабаева, 148/1. <a href='tel:544' className='footerAdress'>Тел: 544</a></p>
                </Col>
                <Col className='col-12 col-md-5 col-lg-5 d-flex justify-content-sm-start justify-content-md-end justify-content-lg-end'>
                    <div>
                        <h3 className='footerCaptionLast text-sm-left text-md-right text-lg-right'>Мы в соц-сетях</h3>
                        <div className='d-flex pt-3 justify-content-sm-start justify-content-md-end justify-content-lg-end'>
                            <a href='https://t.me/bankhumo' target='_blank' className='mr-2' rel = "noopener noreferrer" >
                                <img src={telegramm} alt='Telegram'/>
                            </a>
                            <a href='https://ru-ru.facebook.com/mdohumo' target='_blank' className='mr-2' rel = "noopener noreferrer" >
                                <img src={facebook} alt='Facebook'/>
                            </a>
                            <a href='https://vk.com/humo_tj' target='_blank' className='mr-2' rel = "noopener noreferrer" >
                                <img src={vk} alt='Vkontakte'/>
                            </a>
                            <a href='https://www.instagram.com/humo.tj' target='_blank' rel = "noopener noreferrer" >
                                <img src={instagramm} alt='Instagramm'/> 
                            </a>
                        </div>
                        <div className='footerTell text-sm-left text-md-right text-lg-right'>
                            <a href='tel:+992-88-777-55-44' className='footerTell'>(+992)88-777-55-44</a>
                        </div>
                    </div>
                </Col>
            </Row>
        </Container>
        <Container fluid className='container-fluid footerLast'>
            <Container>
                <Row>
                    <Col className='col-12 col-md-7 col-lg-7 d-flex align-items-center justify-content-center justify-content-md-start justify-content-lg-start footerLastCopyCol'>
                        <p className='footerLastCopy'>© 2020 ЗАО МДО «Хумо»</p>
                    </Col>
                    <Col className='col-12 col-md-5 col-lg-5 d-flex align-items-center justify-content-center justify-content-md-end justify-content-lg-end footerLastCopyCol'>
                        <p className='footerLastText'>Сделано с любовью, сделано для вас</p>
                    </Col>
                </Row>
            </Container>
        </Container>
        </>
    )
}
export default Footer;