import React from 'react'
import './LastAdvantage.css'
import send from '../../../assets/send.svg'
import passport from '../../../assets/passport.svg'
import mini from '../../../assets/mini.svg'
const LastAdvantage = () => {
    return (
        <div style={{background: '#fff'}} id='lastAdvantage'>
            <div className='container lastAdvantageContainer'>
                <h1 className='lastAdvantageCaption'>Преимущества карты «Лахза»</h1>
                <div className='row  lastAdvantageBox'>
                    <div className='col-12 col-md-4 col-lg-4 lastAdvantageBoxItem'>
                        <div className='lastImgBox'>
                            <img src={send} alt='send'/>
                        </div>
                        <h3 className='lastAdvantageContent'>Денежные переводы 
из России </h3>
                        <p className='lastAdvantageText'>Получайте деньги сразу на карту</p>
                    </div>    
                    <div className='col-12 col-md-4 col-lg-4 lastAdvantageBoxItem'>
                        <div className='lastImgBox'>
                            <img src={passport} alt='passport'/>
                        </div>
                        <h3 className='lastAdvantageContent'>Никаких справок</h3>
                        <p className='lastAdvantageText'>Достаточно предоставить паспорт</p>
                    </div>    
                    <div className='col-12 col-md-4 col-lg-4 lastAdvantageBoxItem'>
                        <div className='lastImgBox'>
                            <img src={mini} alt='mini-treck'/>
                        </div>
                        <h3 className='lastAdvantageContent'>Доставка карты</h3>
                        <p className='lastAdvantageText'>Закажите сейчас —  доставим завтра</p>
                    </div>    
                </div> 
            </div>
        </div>
    )
}
export default LastAdvantage;