import React from 'react'
import './Advantage.css'
import { Container } from 'react-bootstrap'
const Advantage = () => {
    return (
        <div style={{background: '#fff'}} id='howToUse'>
            <Container className='advantageBox'>
                <h1 className='advantageCaption'>Как пользоваться картой «Лахза»?</h1>
                <div className='row d-flex justify-content-start align-items-start advantageContainer'>
                    <div className='col-12 col-md-4 col-lg-4 d-flex flex-column justify-content-center align-items-center'>
                        <div className='advantageImg'>1</div>  
                        <h3 className='advantageText'>Ҳумо Online</h3>
                        <p className='advantageLastText'>Добавьте карту в кошелёк и проводите платежи и QR-оплату</p>   
                    </div>
                    <div className='col-12 col-md-4 col-lg-4 d-flex flex-column justify-content-center align-items-center'>
                        <div className='advantageImg'>2</div>
                        <h3 className='advantageText'>Торговые точки</h3>
                        <p className='advantageLastText'>Оплачивайте покупки в магазинах, кафе и других привычных местах</p>
                    </div>
                    <div className='col-12 col-md-4 col-lg-4 d-flex flex-column justify-content-center align-items-center'>
                        <div className='advantageImg'>3</div>
                        <h3 className='advantageText'>Банкоматы</h3>
                        <p className='advantageLastText'>Снимайте наличные деньги в любом банкомате «Корти миллӣ»</p>
                    </div>
                </div>
            </Container>
        </div>
    )
}
export default Advantage;