import React from 'react'
import './ScrollHeader.css'
import {Navbar} from 'react-bootstrap'
import logo from '../../assets/logo.svg'
import { Link} from "react-scroll"
const ScrollHeader = () => {
    return (
        <div style={{transition: '0.5s'}}>
            <div id='scrollHeader' className='scrollHeader'>
                <div className='container'>
                <Navbar  expand="lg" style={{padding: '20px 0'}}>
                <Navbar.Brand href="home"><img src={logo} alt='logo'/></Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                <nav>
                  <Link 
                    activeClass="active"
                    to="autorization"
                    spy={true}
                    smooth={true}
                    duration={500}
                    className='navItem'>Заказать
                  </Link>
                  <Link
                    activeClass="active"
                    to="lastAdvantage"
                    spy={true}
                    smooth={true}
                    duration={500}
                    className='navItem'>Примущество
                  </Link>
                  <Link
                    activeClass="active"
                    to="howToUse"
                    spy={true}
                    smooth={true}
                    duration={500}
                    className='navItem'>Как пользоваться?
                  </Link>
                  <Link
                    activeClass="active"
                    to="guestionsContainer"
                    spy={true}
                    smooth={true}
                    duration={500}
                    className='navItem'>Вопросы и ответы
                  </Link>
                </nav>
                  <h5 className='ml-auto headerPhone align-self-center mt-2'><a href='tel:+992887775544'>(+992)88-777-55-44</a></h5>
                </Navbar.Collapse>
              </Navbar>    
                </div> 
            </div>
        </div> 
    )
}
export default ScrollHeader;