import React from 'react'
// import './AutorizationTest.css'
import './Autorization.css'
import './Toggle.css'
import cardLahza from '../../../assets/lahzaAuto.png'
import { useState,useEffect } from 'react'
import postFunction from '../../customFunction/postFunction'
import InputMask from 'react-input-mask';

const Autorization = () => {
    let initialState = {
        name: '',
        phone: '',
        sec: 60,
        lastName: '',
        nameDisable: false,
        toggle: true,
        disable: true
    }

    
    const [state,setState] = useState(initialState)
    const [data, setData] = useState('')
    const [lastBox,setLastBox] = useState(false)
    const [pin,setPin] = useState('');
    const { name,phone,sec,lastName,nameDisable,toggle,disable} = state    
    let checkSpace = (str) => {
        return str.match(" ")
    }
    let changeInp = (e) => {
        if(e.target.value[e.target.value.length - 1] === " "){
           if(e.target.value[e.target.value.length - 2] !== " " && e.target.value && !checkSpace(e.target.value.slice(0, e.target.value.length - 1))){
               setState({...state,name:e.target.value})
           }
        }
        else if(e.target.value.length > 25 && e.target.value[e.target.value.length - 1].match('^[a-zA-Z]')) {
            if(e.target.value.match(' ')) {
                let newName = e.target.value.split(' ')
                newName = newName[0] + ' ' + newName[1].slice(0,1) + '.'
                setState({...state,lastName:newName,name: e.target.value})
            }
        }
        else if(e.target.value.length > 0 && !e.target.value[e.target.value.length - 1].match('^[a-zA-Z]')) {
            return
        }
        else{
            setState({...state,name:e.target.value})
        }
    }
    const handlerInp = (e) =>{
        if(e.target.value.length > 13) {
         return   
        }
        else if(!e.target.value.includes('+992')) {
            return
        }
        else if(isNaN(e.target.value)) {
            return
        }
        else if(e.target.value[e.target.value.length - 1] === ' '){
            return
        }
        else {
            setState({...state,phone:e.target.value});
        }
    }
    useEffect(() => {
        if(toggle) {
            setState({...state,name: '',nameDisable: false})
        }
        else {
            setState({...state,name:'LAHZA',nameDisable: true})
        }
    },[toggle,lastBox])
    const postUser = () => {
        let newPhone = phone.slice(1)
        let postName = name.length > 25 ? lastName : name
        let newObj = {
            postName,
            newPhone
        }
        postFunction('http://127.0.0.1:4000/getUser',setLastBox,setData,true,newObj)
    }
    const postUserPin = () => {
        let newPhone = phone.slice(1)
        let postName = name.length > 25 ? lastName : name
        let newObj = {
            postName,
            newPhone,
            pin
        }
        postFunction('http://127.0.0.1:4000/getUserPin',setLastBox,setData,false,newObj)
    }
    const testPin = () => {
        if(+pin === data.number) {
            alert('Заявка успешно принята!')
            postUserPin()
            setState(initialState)
            setPin('')
        }
        else {
            alert('error')
        }
    }

    useEffect(() => {
        if(phone.length === 13 && name) {
            setState({...state,disable:false})
        }
        else {
            setState({...state,disable:true})
        }
    },[phone,name])
    useEffect(() => {
        if(lastBox) {
            setState({...state,nameDisable:true,disable: true})
            setTimeout(() => {
                if(sec > 1 && sec <= 60) {
                    setState({...state,sec:sec - 1})
                }
                else {
                    setState({...state,nameDisable:false})
                    setLastBox(false)
                    setPin('')
                } 
            },1000)
        }
        else {
            setState(initialState)
        }
    },[lastBox,sec])
    const [lablePhone,setLabelPhone] = useState(false)
    const [labelName,setLabelName] = useState(false)
    const blurPhone = () => {
        let newPhone = phone;
        newPhone = newPhone.split(' ').join('').split('-').join('').split('(').join('').split(')').join('');
        console.log(newPhone)
        if(newPhone.length < 5) {
           setLabelPhone(false)
        }
        else {
            setLabelPhone(true)
        }
    }
    const handlerPhone = (e) => {
        
        setState({...state,phone: e.target.value})
    }
    return (
        // <Container>
        //     <div className='containerAutorization'>
        //         <h2 className='cardCaption'>Заказать карту</h2>
        //         <div className='row d-flex justify-content-center'>
        //             <div className="d-flex flex-column col-12 col-md-6 col-lg-6 justify-content-center align-items-center">
        //                 <div className="cardBoxInp col-12 d-flex flex-column justify-content-center align-items-center">
        //                 <div className="mt-2 mb-3 mt-md-2 mt-lg-2 d-flex justify-content-center align-items-center">
        //                     <span className="cardSpan">{!toggle ? 'Безимянная' : 'Именная'}</span>
        //                     <input className="tgl tgl-ios" id="cb2" type="checkbox" disabled={lastBox ? true : false} onChange = {(e) => setState({...state, toggle:!toggle})} checked={toggle}/>
        //                     <label className="tgl-btn" htmlFor="cb2"></label>
        //                 </div>      
        //                     <input 
        //                         type="text" 
        //                         className="cardInp form-control" 
        //                         placeholder="Ваше имя"  
        //                         onChange = {changeInp}
        //                         disabled={nameDisable}
        //                         value = {name}
        //                         id="cardInpName"
        //                     />
        //                     <InputMask  
        //                         type="text"
        //                         maskChar = " "
        //                         className = 'cardInp form-control' 
        //                         value = {phone}
        //                         onChange = {handlerInp}
        //                         disabled = {lastBox ? true : false}
        //                         id="cardInpPhone"
        //                     />
        //                 </div>
        //             </div>
        //             <div className='d-flex flex-column col-12 col-md-6 col-lg-6 justify-content-center align-items-center cardContainer'>
        //                 <div style={{position: 'relative'}}>
        //                 <img src={lahza} width={400} className="img-fluid" alt='lahza'/>
        //                 <h3 className='cardName'>{toggle && !name ? 'Ваше имя' : name.length > 25 ? lastName : name}</h3>  
        //                 </div>  
        //             </div>
        //         </div>
        //             <div className='d-flex justify-content-center mt-0 mt-md-5 mt-lg-5 align-items-center flex-column mt-3'>
        //                 <button disabled={disable} className='cardBtn' id="cardBtn" onClick = { postUser }>Подтвердить</button>
        //                 <div className={lastBox ? 'lastCardBox' : 'lastCardBox_none'}>
        //                     <p className='text-center'>Введите СМС-код, отправленный на Ваш номер. СМС-код активен в течение {sec} секунд</p>
        //                     <input 
        //                         type="number" 
        //                         className="form-control" 
        //                         placeholder="Введите СМС код" 
        //                         value = {pin}
        //                         onChange = {(e) => setPin(e.target.value)}
        //                     />
        //                     <button className='cardBtn' id='lastCardBtn' onClick={ testPin }>Подтвердить</button>
        //                 </div>
        //             </div>
        //     </div>
        // </Container>
            <div className='autorization' id='autorization'>
                <div className='container d-flex justify-content-center align-items-center flex-column'>
                    <h3 className='lahzaText'>
                    Заказать карту «Лахза»
                    </h3>
                    <div className='containerAutorization'>
                        <div className='lahzaBox'>
                            <img src={cardLahza} className='img-fluid'/>
                        </div>
                        <div className='inpBox'>
                            <div className='phoneBox'>
                                <label htmlFor='phone' className={!lablePhone ? 'phoneLabel' : 'phoneLabel-active'}>Телефон получателя</label>
                                <InputMask mask='(\+\9\9\2)\ 99-999-99-99' maskChar=''  type='text' className='autoInp' style={lablePhone ? {background:'white'} : null} value={phone} id='phone' onChange={handlerPhone} onFocus={(e) => setLabelPhone(true)} onBlur={ blurPhone }/>
                            </div>
                            <div className='nameBox'>
                                <label htmlFor='name' className={!labelName ? 'nameLabel' : 'nameLabel-active'}>Текстовое поля</label>
                                <input type='text' className='autoInp' value={name} style={labelName ? {background:'white'} : null} id='name' onChange={changeInp} onFocus={(e) => setLabelName(true)} onBlur={ (e) => !name ? setLabelName(false) : null }/>
                            </div>
                            <div className='idCheckbox'>
                            <div style={{width: '35px',marginTop: '5px'}} >
                                <input type="checkbox" class="checkbox" id='checkId'/>
                            </div>
                                <label htmlFor='checkId'>Я даю согласие на обработку моих персональных данных, а также подтверждаю, что ознакомлен(-а) с тарифами карты «Лахза».</label>
                            </div>
                            <button className='lahzaBtn' id='lahzaBtn'>Заказать карту</button>
                        </div>
                    </div>
                </div>
            </div>
    )
}
export default Autorization;