import React from 'react'
import arrowTop from '../../../assets/next_white.svg';
import './MoveTop.css'
import { Link} from "react-scroll";

const MoveTop = () => {
    return (
        <div style={{zIndex: '100'}}>
            <Link id='moveTopBtn'
            activeClass="active"
            to="header"
            spy={true}
            smooth={true}
            duration={500} className='moveTop'>
                <img src={arrowTop} alt='moveTop' />
            </Link>
        </div>
    )
}
export default MoveTop;