import React, {useState} from 'react';
import './App.css';
import Header from './components/Header/Header';
import Autorization from './components/main/Autorization/Autorization';
import Footer from './components/Footer/Footer'
import Guestions from './components/Guestions/Guestions'
import MoveTop from './components/utils/MoveTop/MoveTop'
import Advantage from './components/Advantage/Advantage'
import Capabilities from './components/Capabilities/Capabilities';
import ScrollHeader from './components/ScrollHeader/ScrollHeader';
import LastAdvantage from './components/main/LastAdvantage/LastAdvantage';
const App = () => {
  const [moveTop, setMoveTop] = useState(false);
  let scrollHeader = window.pageYOffset;
  document.addEventListener('scroll', (e) => {
    if(window.pageYOffset > 300) {
      setMoveTop(true)
      if(scrollHeader <= window.pageYOffset) {
        document.getElementById('scrollHeader').classList.add('scrollHeader')
        document.getElementById('scrollHeader').classList.remove('scrollHeader-active')
        scrollHeader = window.pageYOffset
      }
      else if(scrollHeader > window.pageYOffset) {
        document.getElementById('scrollHeader').classList.remove('scrollHeader')
        document.getElementById('scrollHeader').classList.add('scrollHeader-active')
        scrollHeader = window.pageYOffset
      }
    }
    else {
      setMoveTop(false)  
      document.getElementById('scrollHeader').classList.add('scrollHeader')
      document.getElementById('scrollHeader').classList.remove('scrollHeader-active')
    } 
    
  })
  return (
    <>
        <ScrollHeader/>
       <Header/>
       <main>
         <Capabilities/>
         <LastAdvantage/>
        <Autorization/>
         <Advantage/>
        <Guestions/>
       </main>
       <Footer />
       {moveTop 
        ? 
         <MoveTop/>
         :
         null 
        }
    </>
  );
}

export default App;
