import React from 'react'
import './Capabilities.css'
import { Container } from 'react-bootstrap';

const Capabilities = () => {
    return (
        <div style={{background: '#F5F5F5'}}>
            <Container >
                <div className='capabilitiesBox row'> 
                    <div className='col-12 col-md-6 col-lg-3' style={{padding: '0px 10px'}}>
                        <div className='capabilitiesBoxItem '>
                            <div className='box'>
                                <span className='boxFirst'>2</span>
                                <span className='boxSecond'>в 1</span>
                            </div>
                            <h3 className='boxText'>Кредитная и дебетовая карта одновременно</h3>
                        </div> 
                    </div>
                    <div className='col-12 col-md-6 col-lg-3' style={{padding: '0px 10px'}}>
                        <div className='capabilitiesBoxItem '>
                            <div  className='box'>
                                <span className='boxFirst'>0</span>
                                <span className='boxSecond'>%</span>
                            </div>
                            <h3 className='boxText'>Кредиты без комиссии в POS-терминалах «Хумо»</h3>
                        </div>
                    </div>
                    <div className='col-12 col-md-6 col-lg-3' style={{padding: '0px 10px'}}>
                        <div className='capabilitiesBoxItem '>
                            <div  className='box'>
                                <span className='boxFirst'>6</span>
                                <span className='boxSecond'>%</span>
                            </div>
                            <h3 className='boxText'>Годовой доход на остаток денежных средств</h3>
                        </div>
                    </div>
                    <div className='col-12 col-md-6 col-lg-3' style={{padding: '0px 10px'}}>
                        <div className='capabilitiesBoxItem' style={{margin: '0px'}}>
                            <div  className='box'>
                                <span className='boxFirst'>0</span>
                                <span className='boxSecond'>TJS</span>
                            </div>
                            <h3 className='boxText'>Бесплатное оформление и обслуживание карты</h3>    
                        </div>
                    </div>
                </div>
            </Container>
        </div>
    )
}
export default Capabilities;
